.bayan {
    display: flex;
    justify-content: center;
    background-color:#333;
    align-items: center;
    position: absolute;
    left: 49%;
    top: 50%;
    div:first-child {
        animation: slide1 1s linear infinite; 
        position: relative;
    }
    div:last-child {
        animation: slide2 1s linear infinite; 
        position: relative;
    }
}


@keyframes slide1 {
    0% {left:0px; }
    25% { left:-5px; }
    50% { left:-10px; }
    75% { left:-5px; }
    100% { left:0px; }
}

@keyframes slide2 {
    0% {left:0px; }
    25% { left:5px; }
    50% { left:10px; }
    75% { left:5px; }
    100% { left:0px; }
}
