.rooms__listRoom {
	margin-bottom: 1rem;
}
.rooms__alert {
	margin-bottom: 1rem;
}
.rooms__header {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 1em;
}
.rooms__header h2 {
	display: inline-flex;
	margin-bottom: 0;
}
.rooms__cellNumber {
	width: 100px;
}
.rooms__actionsContainer {
	display: flex;
	justify-content: flex-end;
}
.rooms__cellActions {
	width: 100px;
}

.rooms .MuiFormControl-root {
	display: block;
}

.rooms__isLoading {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-40px, -40px);
}